@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap);
.event{display:flex;flex-direction:column;justify-content:space-between;width:148px;height:225px;background:#FFFFFF 0% 0% no-repeat padding-box;box-shadow:0 3px 18px #00000029;border:1px solid #43425D;border-radius:10px;position:absolute;z-index:1000;padding:25px}.event__arrow{position:absolute;top:-16px;left:87px}.event__arrow-change{position:absolute;top:-14px;left:90px}.event__close{position:absolute;top:6px;right:6px}.event__close:hover{cursor:pointer}.event input{outline:none;border-style:none;padding-bottom:10px;border-bottom:1px solid #D6D6D6;width:100%;font-family:"Sourse Sans Pro", sans-serif}.event .error{border-bottom:1px solid #FF5F5F}.event .error::-webkit-input-placeholder{color:#FF5F5F;font-size:12px}.event .error:-ms-input-placeholder{color:#FF5F5F;font-size:12px}.event .error::-ms-input-placeholder{color:#FF5F5F;font-size:12px}.event .error::placeholder{color:#FF5F5F;font-size:12px}.event input[type="text"]{color:#6A6996;padding-left:0;padding-right:0}.event input::-webkit-input-placeholder{color:#D6D6D6;font-family:"Sourse Sans Pro", sans-serif;font-size:9px;line-height:20px;margin-bottom:10px;position:fixed}.event input:-ms-input-placeholder{color:#D6D6D6;font-family:"Sourse Sans Pro", sans-serif;font-size:9px;line-height:20px;margin-bottom:10px;position:fixed}.event input::-ms-input-placeholder{color:#D6D6D6;font-family:"Sourse Sans Pro", sans-serif;font-size:9px;line-height:20px;margin-bottom:10px;position:fixed}.event input::placeholder{color:#D6D6D6;font-family:"Sourse Sans Pro", sans-serif;font-size:9px;line-height:20px;margin-bottom:10px;position:fixed}.event input[type=time]{color:#6A6996;font-family:"Sourse Sans Pro", sans-serif}.event input[type=time]::-webkit-datetime-edit-ampm-field{background-color:#7155d3;border-radius:15%;color:#fff;padding:19px 13px}.event input[type=time]::-webkit-clear-button{display:none}.event input[type=time]::-webkit-inner-spin-button{display:none}.event__date :nth-child(2){position:absolute;right:25px;top:75px}.event__time :nth-child(2){position:absolute;right:25px;top:125px}.event__buttons{display:flex;flex-direction:row;justify-content:space-between;font-size:12px;line-height:25px}.event__buttons :nth-child(1){color:#FF5F5F}.event__buttons :nth-child(1):hover{cursor:pointer}.event__buttons :nth-child(2){color:#6A6996}.event__buttons :nth-child(2):hover{cursor:pointer}.disable{display:none}

.calendar{font-family:'Sourse Sans Pro', sans-serif;background-color:#ffffff;padding:20px;box-shadow:0 2px 6px #0000000A}.calendar__title{color:#4D4F5C;font-size:18px;line-height:13px}.rbc-event{background-color:#3B86FF}.rbc-btn-group{border:1px solid #D7DAE2 !important;margin-top:15px;border-radius:4px}.rbc-btn-group button:hover{cursor:pointer;color:#3B86FF;background-color:#ffffff}.rbc-btn-group .rbc-active{color:#3B86FF !important;background-color:#fff !important;box-shadow:none !important;border:1px solid #D7DAE2 !important}.rbc-month-header{border:1px solid #EAF0F4}.rbc-month-view .rbc-date-cell{padding-right:15px}.rbc-month-view .rbc-date-cell a{color:#43425D;font-size:15px;line-height:35px}.rbc-header{display:flex;justify-content:center;align-items:center;background-color:#F5F6FA;height:45px;border-left:none !important}.rbc-header span{color:#A3A6B4;font-size:11px;line-height:14px;text-transform:uppercase}.rbc-toolbar{margin-bottom:20px}.rbc-toolbar .rbc-toolbar-label{font-weight:bold;font-size:18px;line-height:13px;color:#4D4F5C}.rbc-off-range-bg{background-color:#ffffff}.rbc-off-range a{color:#43425D;opacity:0.3}.rbc-selected{border:1px solid #3B86FF;box-shadow:0 3px 6px #00000063;color:#3B86FF;background-color:#F5F6FA !important}.rbc-events-container{background-color:#F5F6FA;margin-right:0 !important}

.sidebar{display:flex;flex-direction:column;background-color:#43425D;width:260px;font-family:"Sourse Sans Pro", sans-serif;position:absolute;top:0}.sidebar__header{text-transform:uppercase;padding:26px;color:#ffffff;font-weight:bold;font-size:15px;line-height:21px;letter-spacing:3px;background-color:#35334b}.sidebar__icon{margin-right:12px}.sidebar__list{list-style:none;font-size:15px;line-height:19px;padding-left:0}.sidebar__list li{display:flex;padding:17px 17px 26px}.sidebar__list li:hover{cursor:pointer}.sidebar__list a{color:#ffffff;text-decoration:none}.sidebar .active{background-color:#35334b;border-left:5px solid #A3A0FB}

.header{display:flex;flex-direction:row;justify-content:space-between;align-items:center;height:70px;left:260px;position:absolute;width:calc(100% - 260px);background-color:#ffffff}.header__search{display:flex;padding-left:20px}.header__search input{padding-left:10px;width:auto;max-width:600px;border:none;outline:none}.header__search input::-webkit-input-placeholder{font-family:"Sourse Sans Pro", sans-serif;line-height:20px;font-size:13px;color:#4D4F5C}.header__search input:-ms-input-placeholder{font-family:"Sourse Sans Pro", sans-serif;line-height:20px;font-size:13px;color:#4D4F5C}.header__search input::-ms-input-placeholder{font-family:"Sourse Sans Pro", sans-serif;line-height:20px;font-size:13px;color:#4D4F5C}.header__search input::placeholder{font-family:"Sourse Sans Pro", sans-serif;line-height:20px;font-size:13px;color:#4D4F5C}.header__icons{display:flex}.header__icons img{margin-right:30px}.header__user-block{display:flex;flex-direction:row}.header__user{display:flex;align-items:center;color:#4D4F5C;padding-left:20px;font-size:13px;font-family:"Sourse Sans Pro", sans-serif;line-height:20px;border-left:1px solid #EBEBF2;padding-right:20px}.header__user-name{margin-right:15px}.header__user-avatar{margin-left:11px}

body{margin:0}.app{display:flex;flex-direction:column;height:auto}.app__content{display:flex;flex-direction:column;justify-content:space-between;background-color:#F0F0F7;height:100%;margin:0 auto;padding-left:260px;width:calc(100% - 260px)}.app__content-block{margin:170px 75px 100px 75px}.app__content-title{position:absolute;top:100px;font-family:"Sourse Sans Pro", sans-serif;font-size:28px;line-height:40px;color:#43425D}

